import { api, tags, urlUtils } from '@igblsln/store';
export const weeklyPaymentApi = api.injectEndpoints({
    overrideExisting: false,
    endpoints: (build) => ({
        listWeeklyPurchasePayment: build.query({
            query: ({ weeklyId }) => {
                return {
                    url: urlUtils('/payment/purchase/', `?weekly_id=${weeklyId || ''}`),
                };
            },
            providesTags: (result = []) => [
                ...result.map(({ key: id }) => ({ type: tags.PROJECT_PURCHASE_INQUIRY, id })),
                { type: tags.PROJECT_PURCHASE_INQUIRY, id: 'LIST' },
            ],
        }),
        getErrorProne: build.query({
            query: () => 'error-prone',
        }),
    }),
});
export const { useListWeeklyPurchasePaymentQuery, useGetErrorProneQuery } = weeklyPaymentApi;
